  <div id="Usuario">
    <br />
    <b-col cols="12" md="9" lg="8" xl="7" class="m-auto">
      <UserCard />
    </b-col>
  </div>
</template>

<script>
import UserCard from '@/components/UserCard.vue'
export default {
  name: 'Usuario',
  components: {
    UserCard,
  },
  data() {
    return {
      value: '',
      context: null,
    }
  },
  created() {
    if (
      location.href.split('?id=')[1] == undefined ||
      location.href.split('?id=')[1] == ''
    ) {
      this.$router.go(-1)
    }
  },
  methods: {
    onContext(ctx) {
      this.context = ctx
    },
  },
}
</script>

