<template>
  <div>
    <div class="c m-2 p-2 d-md-flex justify-content-between align-items-center">
      <img
        id="imagenFotoPerfil"
        :src="src + usuario.fotoPerfil"
        class="c-img rounded-circle"
        alt="User Image"
      />

      <div class="c-body m-5 d-flex flex-column align-items-md-end">
        <h5 id="name" class="c-title">{{ usuario.nombre }}</h5>
        <div class="c-text">
          <p>@{{ usuario.usuario }}</p>
          <br />
          <span v-if="usuario.tatuador == true" class="my-3">tattooer üë®‚Äçüé®</span>
          <span v-else class="my-3">tattooed üôç‚Äç‚ôÇÔ∏è</span>
        </div>
        <p
          v-if="usuario.aboutYou == '' && accion"
          class="delete"
          title="edit"
          @click="editAboutYou()"
        >
          Cuenta algo sobre ti!
        </p>
        <p v-else-if="accion" class="editable" title="edit" @click="editAboutYou()">
          {{ usuario.aboutYou }}
        </p>
        <p v-else>
          {{ usuario.aboutYou }}
        </p>

        <form action="" @submit.prevent="actualizarUsuario(usuario)">
          <div class="ok">
           <input
              type="text"
              hidden
              v-on:keyup="cuentaLetras"
              class="edit form-control m-2"
              v-model="usuario.aboutYou"
            />
            <button
              type="submit"
              hidden
              class="ok btn btn-outline-success mb-2"
              @click="hidden()"
            >
              edit {{ cont }}/200
            </button>
          </div>
        </form>
        <div class="d-flex align-items-center justify-content-center">
          <button
            id="citas"
            type="button"
            class="btn btn-primary p-0 px-3"
            v-if="accion"
            v-b-toggle="'desplegable'"
          >
            Citas
            <span class="badge badge-light">{{ citas.length }}</span>
          </button>
          <b-dropdown text="üõ†" variant="warning" class="m-2" v-if="accion">
            <b-dropdown-item>
              <p @click="editAboutYou()" title="edit">
                üí¨ Editar bio
              </p>
            </b-dropdown-item>
            <b-dropdown-item>
              <p @click="editarUsuario(usuario._id)" title="edit">
                üôç‚Äç‚ôÇÔ∏è Editar usuario
              </p>
            </b-dropdown-item>
            <b-dropdown-item>
              <p @click="eliminarUsuario(usuario._id)" title="delete">
                üí• Eliminar usuario
              </p>
            </b-dropdown-item>
          </b-dropdown>
        </div>
      </div>
    </div>
    <div class="container">
      <b-alert
        :show="dismissCountDown"
        :variant="mensaje.color"
        @dismissed="dismissCountDown = 0"
        @dismiss-count-down="countDownChanged"
      >
        <p>{{ mensaje.texto }}</p>
        <b-progress
          :variant="mensaje.color"
          :max="dismissSecs"
          :value="dismissCountDown"
          height="4px"
        ></b-progress>
      </b-alert>
    </div>
    <b-collapse id="desplegable">
      <b-card
        v-for="(item, index) in citas"
        :key="index"
        no-body
        class="mb-1 mx-5"
      >
        <b-card-header header-tag="header" role="tab">
          <div
            class="d-flex flex-wrap align-items-center justify-content-around"
          >
            <div class="boton" style="min-width: 90px;">
              <p>Fecha</p>
              <p>{{ item.fechaCita.substring(0, 10) }}</p>
            </div>
            <div class="boton">
              <p>Autorizado</p>
              <p v-if="item.autorizado == true">‚úÖ</p>
              <p v-else>‚ùî</p>
            </div>
            <div class="boton mx-3">
              <p>‚Ç¨</p>
              <p>{{ item.precio }}</p>
            </div>
            <div class="boton">
              <p>
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="16"
                  height="16"
                  fill="currentColor"
                  class="bi bi-three-dots"
                  viewBox="0 0 16 16"
                >
                  <path
                    d="M3 9.5a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3zm5 0a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3zm5 0a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3z"
                  />
                </svg>
              </p>
              <b-button v-b-toggle="'accordionusu-' + index">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="16"
                  height="16"
                  fill="currentColor"
                  class="bi bi-info-circle-fill"
                  viewBox="0 0 16 16"
                >
                  <path
                    d="M8 16A8 8 0 1 0 8 0a8 8 0 0 0 0 16zm.93-9.412-1 4.705c-.07.34.029.533.304.533.194 0 .487-.07.686-.246l-.088.416c-.287.346-.92.598-1.465.598-.703 0-1.002-.422-.808-1.319l.738-3.468c.064-.293.006-.399-.287-.47l-.451-.081.082-.381 2.29-.287zM8 5.5a1 1 0 1 1 0-2 1 1 0 0 1 0 2z"
                  />
                </svg>
              </b-button>
            </div>
          </div>
        </b-card-header>
        <b-collapse :id="`accordionusu-${index}`">
          <b-card-body>
            <b-card-text>
              <div class="abajo d-flex flex-wrap">
                <div class="boton">
                  <p>Autor</p>
                  <button
                    class="btn btn-success"
                    @click="perfil(item.tatuador)"
                  >
                    üë®‚Äçüé®
                  </button>
                </div>
                <div class="boton">
                  <p>Cliente</p>
                  <button class="btn btn-success" @click="perfil(item.cliente)">
                    üôé‚Äç‚ôÇÔ∏è
                  </button>
                </div>
                <div class="boton">
                  <p>Tatuaje</p>
                  <button
                    class="btn btn-primary"
                    @click="tatuajeE(item.tatuaje)"
                  >
                    üé®
                  </button>
                </div>
                <div class="boton">
                  <p>Acciones</p>
                  <button
                    @click="showMsgBox(item._id)"
                    class="btn btn-danger"
                    title="delete"
                  >
                    üí•
                  </button>
                </div>
              </div>
            </b-card-text>
          </b-card-body>
        </b-collapse>
      </b-card>
    </b-collapse>
  </div>
</template>

<script>
export default {
  name: "perfil",
  data() {
    return {
      box: '',
      cont: '',
      dismissSecs: 5,
      dismissCountDown: 0,
      showDismissibleAlert: false,
      mensaje: {
        color: '',
        texto: '',
      },
      usuario: {
        nombre: '',
        tatuador: Boolean,
        fechaNacimiento: '',
        fotoPerfil: '',
        usuario: '',
        password: '',
        aboutYou: '',
      },
      src: 'http://localhost:3000/',
      accion: false,
      paginate: ['citas'],
      citas: [],
      cita: {
        _id: '',
        tatuador: '',
        cliente: '',
        fechaCita: '',
        precio: '',
        autorizado: Boolean,
        tatuaje: '',
      },
      idTem: "",
    }
  },
  created() {
    var id
    if(this.idTem=="")
      id = location.href.split('?id=')[1]
    else
      id = this.idTem;
    this.axios
      .get('usuarios/listar/' + id)
      .then((res) => {
        if (
          localStorage.getItem('tatuador') == 'true' ||
          localStorage.getItem('id') == id
        ) {
          this.accion = true
        } else if (!res.data.tatuador && localStorage.getItem('id') != id) {
          this.$router.go(-1)
        }
        this.usuario = res.data
        this.usuario.fechaNacimiento = res.data.fechaNacimiento.substring(0, 10)
      })
      .catch((e) => {
        console.log(e)
      })
      .finally((e) => {
        this.axios
          .get('citas/usuario/' + id)
          .then((res) => {
            this.citas = res.data
            this.citas.sort(function (a, b) {
              return (
                new Date(a.fechaCita).getTime() -
                new Date(b.fechaCita).getTime()
              )
            })
          })
          .catch((e) => {
            console.log(e)
          })
      })
  },
  methods: {
    alerta() {
      this.mensaje.color = 'danger'
      this.mensaje.texto = 'Cita cancelada '
      this.showAlert()
    },
    alertaF() {
      this.mensaje.color = 'danger'
      this.mensaje.texto = 'No puede escribir m√°s de 200 caracteres '
      this.showAlert()
    },
    actualizarUsuario(item) {
      console.log(item.aboutYou.length)
      if(item.aboutYou.length<=200){
         this.axios
        .put('usuarios/actualizar/' + item._id, item)
        .then((res) => {
          this.usuario.aboutYou = res.data.aboutYou
        })
        .catch((e) => {
          console.log(e.response)
        })
      }
    },
    editarUsuario(id) {
      window.location.href = '/usuarios/editar?id=' + id
    },
    eliminarUsuario(id) {
      this.axios.delete('/usuarios/borrar/' + id).then((res) => {
        const index = this.usuarios.findIndex(
          (item) => item._id === res.data._id,
        )
        this.usuarios.splice(index, 1)
        localStorage.clear()
        this.mensaje.color = 'danger'
        this.mensaje.texto = 'Usuario eliminado con exito!'
        this.showAlert()
      })
    },
    editAboutYou() {
      document.querySelector('input.edit').removeAttribute('hidden')
      document.querySelector('input.edit').focus()
      document.querySelector('button.ok').removeAttribute('hidden')
      this.cont = document.querySelector('input.edit').value.length
    },
    hidden() {
      if (this.cont > 200) {
      } else {
        document.querySelector('input.edit').setAttribute('hidden', '')
        document.querySelector('button.ok').setAttribute('hidden', '')
      }
    },
    perfil(id) {
      window.location.href = '/usuario?id=' + id
    },
    tatuajeE(id) {
      window.location.href = '/tatuaje?id=' + id
    },
    eliminarCita(id) {
      const cita = this.citas.find((item) => item._id === id)
      this.axios.delete('/citas/borrar/' + id).then((res) => {
        const index = this.citas.findIndex((item) => item._id === res.data._id)
        this.citas.splice(index, 1)
        this.alerta()
      })
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs
    },
    showMsgBox(item) {
      this.boxTwo = ''
      this.$bvModal
        .msgBoxConfirm('¬øQuieres eliminarlo?', {
          title: 'Por favor, confirme',
          size: 'sm',
          buttonSize: 'sm',
          okVariant: 'danger',
          okTitle: 'ELIMINAR',
          cancelTitle: 'CANCELAR',
          footerClass: 'p-2',
          hideHeaderClose: false,
          backgroundColor: 'success',
          centered: true,
        })
        .then((value) => {
          this.box = value
          if (this.box == true) {
            this.eliminarCita(item)
          }
        })
        .catch((err) => {
          console.log(err)
        })
    },
    cuentaLetras() {
      this.cont = document.querySelector('input.edit').value.length
      if (this.cont > 200) {
        document
          .querySelector('button.ok')
          .classList.remove('btn-outline-success')
        document.querySelector('button.ok').classList.add('btn-danger')
      } else if (
        !document
          .querySelector('button.ok')
          .classList.contains('btn-outline-success')
      ) {
        document.querySelector('button.ok').classList.remove('btn-danger')
        document.querySelector('button.ok').classList.add('btn-outline-success')
      }
    },
  },
}
</script>

<style>
h5#name {
  color: #243241;
}
button > ul {
  background-color: #243241;
}
button > ul > li > a > p {
  color: #82a5c7;
}
div.c {
  background-color: #82a5c7;
  color: #243241;
  border-radius: 20px;
  margin-top: 0 !important;
  min-width: 287px;
}
#imagenFotoPerfil {
  object-fit: cover;
  width: 270px;
  height: 270px;
}
.delete {
  cursor: pointer;
}
input {
  background-color: #4e647a !important;
  color: #82a5c7 !important;
}
div > button#citas {
  height: 38px;
}
.row {
  margin-right: 0 !important;
}
div.abajo {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}
</style>
